import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConfig } from '../../app/app.config';
//import { AppAvailability } from '@ionic-native/app-availability';
//let app;
//let startApp;
/**
 * Generated class for the StudyPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-study',
    templateUrl: 'study.html',
})
export class StudyPage {
    xueshiGeren = 0;
    jifen = "";
    xueshiJiti=0;
    constructor(public navCtrl: NavController, public navParams: NavParams, public alertCtrl: AlertController,
        private http: Http) {
    }
    ionViewWillEnter(){
        this.xueshiGeren = Number.parseFloat((localStorage.getItem("xfwPoints") || "0")) || 0;
        this.getPoints();
    }
    getPoints() {
        if(localStorage.getItem("djycookie"))
        this.http.get(AppConfig.getUrl() + "/djySpider/userInfo", { params: { cookie: localStorage.getItem("djycookie") } }).subscribe(r => {
            let j = r.json();
            if (j.status) {
                this.jifen = j.userInfo["userinfo-exp"];
            }
        }, e => {

        });
        if(localStorage.getItem("xfwcookie"))
        this.http.get(AppConfig.getUrl() + "/xfwSpider/xueshi", { params: { cookie: localStorage.getItem("xfwcookie") } }).subscribe(r => {
            let j = r.json();
            if (j.status) {
                let nickname=localStorage.getItem("nickname");
                j.data.forEach(element => {
                    if(element.text[1]&&element.text[5]){
                        if(element.text[1]==nickname){
                            this.xueshiJiti=this.xueshiJiti+(Number.parseInt(element.text[5])||0);
                        }
                    }
                });
            }
        }, e => {

        });
    }
    ionViewDidLoad() {
        console.log('ionViewDidLoad StudyPage');
    }

    goto(id) {
        switch (id) {
            case 1:
                //this.out();
                break;
            case 2:
                this.navCtrl.push("StudymeansPage");
                break;
            case 3:
                this.navCtrl.push("StudyvideoPage");
                break;
        }

    }
    /* out(){
        if (this.platform.is('ios')) {
            app = 'twitter://';
        } else if (this.platform.is('android')) {
            app = 'com.twitter.android';
        }
        this.appAvailability.check(app)
            .then(
                (yes: boolean) => {
                    var sApp = startApp.set({
                        "component": app,
                    });
                    sApp.start(function () { 
                        console.log("OK");
                    }, function (e) {
                        console.log(e);
                     })
                },
                (no: boolean) => {
                    let alert = this.alertCtrl.create({
                        title: "未安装党建云app，请先进行安装"
                    });
                    alert.present();
                }
            );
    } */
}



